.header {

  &-top {
      padding-top: 15px;
      padding-bottom: 13px;
      background-color: var(--color-mine-shaft);

      &__container {
          display: flex;
          align-items: center;
          justify-content: space-between;

          @include _1150 {
            padding: 0;
          }
      }

      &__right {
          width: 75%;
          display: flex;
          align-items: center;
          justify-content: space-between;

          @include _1195 {
            width: initial;
          }
      }
  }


  &__location {

    @include _1150 {
      display: none;
    }

      svg {
          display: inline-block;
          margin-right: 4px;
          width: 10px;
          height: 10px;
          transition: fill .3s ease-in-out;
          fill: #FAFBFC;
      }

      &:hover svg {
          transition: fill .3s ease-in-out;
          fill: var(--color-bright-sun);
      }

      &:hover .location__city {
          transition: color .3s ease-in-out;
          color: var(--color-bright-sun);
      }

      &:hover .location__city::before {
        transition: background-color .3s ease-in-out;
        background-color: var(--color-bright-sun);
      }

      .location__city {
          position: relative;
          font-weight: 500;
          transition: color .3s ease-in-out;
          color: var(--color-athens-gray);

          &::before {
            @include ghost;
            height: 1px;
            top: initial;
            bottom: 1px;
            transition: background-color .3s ease-in-out;
            background-color: var(--color-athens-gray);
          }
      }
  }

  &__number {
      font-size: 18px;
      font-weight: 500;
      transition: color .3s ease-in-out;
      color: var(--color-athens-gray);

      @include _1150 {
        display: none;
      }

      &:hover {
          transition: color .3s ease-in-out;
          color: var(--color-bright-sun);
      }

      svg {
          display: inline-block;
          margin-right: 5px;
          width: 13px;
          height: 13px;
          transition: stroke .3s ease-in-out;
          stroke: var(--color-white);
      }

      &:hover svg {
          transition: stroke .3s ease-in-out;
          stroke: var(--color-bright-sun);
      }

  }

  &__number--mobile {
    display: none;
    margin-right: 22px;
    stroke: var(--color-tundora);

    & {
      @include _1150 {
        display: block;
      }
    }
  }

  .header-bottom {
      position: relative;
      z-index: 150;
      padding-top: 9px;
      padding-bottom: 10px;
      background-color: var(--color-white);
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);

      @include _675 {
        height: 136px;
      }

      @include _480 {
        padding-top: 20px;
        height: 128px;
      }

      &__container {
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      &__wrapper {
        display: flex;
        align-items: center;

        @include _1150 {
          padding-left: 6px;
        }

        @include _675 {
          width: 65%;
          justify-content: space-between;
        }

        @include _480 {
          padding-left: 3px;
        }
      }

      .header__logo {
          padding-top: 10px;

          @include _480 {
            padding-top: 0;
          }
      }

      &__right {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 75%;

          @include _1150 {
            width: 66%;
          }

          @include _675 {
            width: auto;
          }
      }

      &__form {
        margin-right: 10px;
          padding: 2px;
          width: 75%;
          border: 1px solid var(--color-matisse);
          border-radius: var(--border-radius);
          display: flex;
          align-items: center;

          @include _1150 {
            width: 79%;
            flex-direction: row-reverse;
          }

          @include _675 {
            position: absolute;
            bottom: 16px;
            left: 8px;
            right: 8px;
            margin-right: 0;
            width: auto;
          }

          @include _480 {
            bottom: 15px;
          }
      }

      &__input {
          width: 100%;
          height: 37px;
          padding-left: 12px;
          border: none;

          @include _1150 {
            padding-left: 0;
          }
      }

  }

}
