.footer-accordion__list {
  display: none;
  margin-bottom: 18px;

  @include _675 {
    display: block;
  }
}

.footer-accordion__item {
   border-bottom: 1px solid rgba(205, 203, 203, 0.2);
}

.footer-accordion__title {
  @include _480 {
    font-size: 14px;
    font-weight: 500;
  }
}

.footer-accordion__icon {
  display: inline-block;
  width: 12px;
  height: 6px;
  background-image: url("../img/footer-arrow.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 0.3s ease-out;
}

.footer-accordion__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 8px;
  width: 100%;
  text-align: left;
  color: var(--color-athens-gray);
  background-color: transparent;
  cursor: pointer;

  @include _480 {
    padding: 20px 8px;
  }
}

.footer-accordion__content {
  overflow: hidden;
  padding: 0 9px 0;
  max-height: 0;
  box-sizing: content-box;
  opacity: 0;
  transition: all 0.3s ease-out;
  will-change: max-height;
}

.open .footer-accordion__icon {
  transform: rotate(180deg);
}

.open .footer-accordion__content {
  padding-bottom: 18px;
  opacity: 1;

  @include _480 {
    padding-bottom: 11px;
  }
}
