.about {
  padding-top: 35px;
  padding-bottom: 82px;


  @include _1150 {
    padding-top: 10px;
    padding-bottom: 30px;
  }

  @include _480 {
    padding-bottom: 32px;
  }

  &__heading {
    font-size: 36px;
    margin: 0;
    margin-bottom: 48px;

    @include _1150 {
      font-size: 16px;

      margin-bottom: 9px;
    }
  }

  &__text {
    font-size: 14px;
    margin: 0;
    line-height: 21px;
    color: var(--color-tundora);

    @include _1150 {
      font-size: 13px;
    }

    @include _480 {
      line-height: 20px;
    }
  }
}
