.burger {
    display: none;
    margin-right: 24px;
    width: 21px;
    height: 14px;
    position: relative;

    @include _1150 {
      display: block;
    }

    @include _480 {
      width: 27px;
    }

    &__line {
      height: 2px;
      width: 100%;
      display: inline-block;
      position: absolute;
      background-repeat: no-repeat;
      background-position: center;
      transition: transform .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out,
      top .3s ease-in-out;
      background-image: url("../img/burger-line.svg");
  }

  &__line:nth-child(1) {
      top: 0;
      right: 0;

      &.burger-open {
        top: 7px;
        transform: rotate(45deg);
        transition: transform .3s ease-in-out, top .3s ease-in-out;
      }
  }

  &__line:nth-child(2) {
      right: 0;
      top: 41%;

      &.burger-open {
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
      }
  }

  &__line:nth-child(3) {
      bottom: 0;
      right: 0;

      &.burger-open {
        bottom: 5px;
        transition: transform .3s ease-in-out, top .3s ease-in-out;
        transform: rotate(-45deg);
      }
  }
}

.burger-menu__item {
  display: none;
}

.burger-menu__item--desctop {
  display: block;
}

@include _1150 {
  .burger-menu {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: transparent;
    padding: 0;
    background: rgba(0, 0, 0, 0.6);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

    &__content {
      padding: 25px 16px 24px;
      position: absolute;
      border-bottom-right-radius: var(--border-radius);
      opacity: 0;
      left: 0;
      top: -100vw;
      visibility: hidden;
      z-index: 100;
      min-width: 320px;
      transition: top .3s ease-in-out,
      opacity .3s ease-in-out,
      visibility .3s ease-in-out;
      background-color: var(--color-white);
    }

    .header-top {
      &__container, &__right {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    &__item {
      display: block;
      display: flex;
      align-items: center;
    }

    &__icon {
      display: inline-block;
      position: relative;
      width: 32px;
      height: 32px;
      margin-right: 17px;
      clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 15px;
      background-color: var(--color-pretty-grey);
    }

    &__city {
      font-size: 14px;
      display: inline-block;
      margin-right: 30px;
      color: var(--color-boulder);
    }

    &__select {
      border: none;
    }
  }

  .nav {

    &__list {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .burger-menu--active {
    opacity: 1;
    visibility: visible;
    transition: top .3s ease-in-out,
    opacity .3s ease-in-out,
    visibility .3s ease-in-out;
    top: 0;
  }
}
