.hits {
    padding-top: 24px;
    padding-bottom: 34px;

    @include _1150 {
      padding-top: 15px;
      padding-bottom: 15px;
    }

    @include _480 {
      padding-top: 10px;
    }

    &__heading {
        margin: 0;
        margin-bottom: 41px;

        @include _1150 {
          margin-bottom: 9px;
        }
    }

    &__content {
      padding: 7px 5px 13px 3px;
      overflow: hidden;
    }

    &__slider {
      position: relative;
      height: 100%;
    }

    &-slide {
      // max-width: 254px;
      position: relative;
      padding-top: 24px;
      padding-bottom: 32px;
      text-align: center;
      background-color: var(--color-white);
      height: 100%;
      transition: color .3s ease-in-out;
      box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
      border-radius: var(--border-radius);

      @include _1150 {
        width: 252px !important;
        padding-bottom: 25px;
      }

      @include _675 {
        width: 190px !important;
      }

      @include _480 {
        width: 160px !important;
        padding-bottom: 27px;
        padding-top: 0;
      }

      &:hover .hits-slide__name {
        transition: color .3s ease-in-out;
        color: var(--color-sunset-orange);
      }

      img {
        margin-bottom: 20px;

        @include _1150 {
          margin-bottom: 3px;
        }

        @include _480 {
          margin-bottom: 0;
        }
      }

      &__inner {
        padding: 0 16px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;

        @include _1150 {
          padding: 0 9px;
        }
      }

      &__name {
        display: block;
        margin-bottom: 24px;
        text-align: start;
        font-size: 18px;
        line-height: 24px;
        transition: color .3s ease-in-out;
        color: var(--color-tundora);

        @include _1150 {
          font-size: 15px;
          line-height: 21px;
          margin-bottom: 17px;
        }

        @include _480 {
          line-height: 18px;
        }

        &::before {
          @include ghost;
        }

        &:hover {
          transition: color .3s ease-in-out;
          color: var(--color-mandy);
        }
      }

      &__vendor {
        display: block;
        margin-bottom: 35px;
        font-size: 14px;
        color: var(--color-boulder);

        @include _1150 {
          font-size: 13px;
          margin-bottom: 26px;
        }

        @include _480 {
          margin-bottom: 30px;
        }
      }

      &__price {
        font-size: 20px;
        font-weight: 700;
        color: var(--color-mine-shaft);

        @include _480 {
          font-size: 17px;
          font-weight: 500;
        }
      }
    }
}
