.nav {

  @include _1150 {
    width: 100%;
  }

    &__list {
        display: flex;
        align-items: center;

        @include _1150 {
          width: 100%;
        }
    }

    &__item {

      &:not(:last-child) {
        margin-right: 52px;
      }

      @include _1150 {
        width: 100%;

        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 13px;
        }

        &:nth-child(5) {
         padding-bottom: 13px;
         border-bottom: 1px solid var(--color-mercury);
        }

        &:nth-child(9) {
          padding-bottom: 13px;
          border-bottom: 1px solid var(--color-mercury);
         }
      }

    }

    &__link {
        font-weight: 500;
        transition: color .3s ease-in-out;
        color: var(--color-athens-gray);

        @include _1150 {
          font-size: 16px;
          font-weight: 400;
          color: var(--color-tundora);
        }

        &:hover {
            transition: color .3s ease-in-out;
            color: var(--color-bright-sun);
        }
    }

}
