.footer {
  padding-top: 50px;
  padding-bottom: 38px;
  background-color: var(--color-mine-shaft);

  @include _1150 {
    padding-top: 24px;
    padding-bottom: 84px;
  }

  @include _1150 {
    &__container {
      display: flex;
      flex-direction: column;
      position: relative;
    }
  }

  @include _675 {
    padding-top: 8px;
  }

  @include _480 {
    padding-bottom: 78px;
  }

  &-top {
    display: flex;
    padding-bottom: 26px;
    border-bottom: 1px solid rgba(205, 203, 203, 0.2);

    @include _1150 {
      border: none;
      padding-bottom: 13px;
    }

    @include _675 {
      flex-direction: column;
    }

    @include _480 {
      padding-bottom: 5px;
    }

    &__block {
      min-height: 267px;
      // width: calc(30% - 85px);
      padding-left: 16px;
      border-left: 1px solid rgba(205, 203, 203, 0.2);

      @include _1150 {
        min-height: auto;
      }

      @include _675 {
        border-left: none;
        padding-left: 8px;
      }

      &--logo {
        border: none;
        padding: 0;
        width: 24%;

        @include _1150 {
          width: calc(33% + 6px);
          position: relative;

          &::before {
            @include ghost;
            left: initial;
            right: 0;
            width: 1px;
            height: calc(100% + 77px);
            background-color: rgba(205, 203, 203, 0.2);
          }
        }

        @include _675 {
          display: none;
        }
      }

      &--shop {
        width: 43%;
        margin-right: 52px;

        @include _1150 {
          width: auto;
          margin: 0;
          margin-bottom: 16px;
          border: none;
        }

        @include _675 {
          display: none;
        }
      }

      &--company {
        margin-right: 17px;

        @include _1150 {
          border: none;
        }

        @include _675 {
          display: none;
        }
      }

      &--contacts {
        .footer-top__item {
          margin-bottom: 20px;
        }

        @include _1150 {
          .footer-top__title {
            margin-bottom: 13px;

            @include _480 {
              margin-bottom: 18px;
            }
          }

          .footer-top__item {
            margin-bottom: 12px;
          }

        }
      }
    }

    @include _1150 {
      &__logo {
        display: block;
        width: 194px;
      }
    }

    &__wrapper  {
      display: flex;
      width: calc(50% + 10px);

      @include _1150 {
        flex-direction: column;
        width: 33%;
        margin-right: 29px;
      }

      @include _675 {
        width: 100%;
      }
    }

    &__title {
      margin: 0;
      margin-bottom: 22px;
      font-weight: 500;
      font-size: 18px;
      color: var(--color-athens-gray);

      @include _1150 {
        font-size: 14px;
        margin-bottom: 9px;
      }
    }

    &__item {
      margin-bottom: 8px;

      @include _1150 {
        margin: 0;
      }
    }

    &__link {
      color: var(--color-alto);
      transition: color .3s ease-in-out;

      &:hover {
        transition: color .3s ease-in-out;
        color: var(--color-golden-tainoi);
      }

      @include _1150 {
        font-size: 12px;
      }
    }

    &__subtitle {
      margin-bottom: 8px;
      display: block;
      color: var(--color-silve);

      @include _1150 {
        font-size: 12px;
        margin-bottom: 3px;
      }

      @include _675 {
        display: none;
      }
    }

    &__contacts {
      padding-left: 24px;
      font-weight: 500;
      font-size: 18px;
      background-position: left;
      background-repeat: no-repeat;
      color: var(--color-athens-gray);

      @include _1150 {
        font-size: 14px;
      }

      &--address {
        background-image: url("../img/marker.svg");
      }

      &--phone {
        background-image: url("../img/futer-phone.svg");
      }

      &--email {
        background-image: url("../img/mail.svg");
      }
    }

    &__logo--mobile {
      display: none;

      @include _675 {
        display: block;
        margin-bottom: 16px;
      }

      @include _480 {
        margin-bottom: 23px;
      }
    }
  }

  &-bottom {
    &__inner {
      display: flex;
      padding-top: 24px;
      padding-bottom: 24px;
      border-bottom: 1px solid rgba(205, 203, 203, 0.2);

      @include _1150 {
        border-top: 1px solid rgba(205, 203, 203, 0.2);
        border-bottom: 0;
        padding-top: 17px;
        padding-bottom: 16px;
      }

      @include _675 {
        border-bottom: 1px solid rgba(205, 203, 203, 0.2);
        margin-bottom: 16px;
        justify-content: space-between;
      }

      @include _480 {
        flex-direction: column;
        padding-left: 10px;
        padding-bottom: 24px;
      }
    }

    @include _1150 {
      display: inline-block;
      align-self: flex-end;
      width: calc(((100% - 33%) - 6px) - 16px);
    }

    @include _675 {
      display: block;
      align-self: stretch;
      width: 100%;
    }

    &__block {
      width: 50%;
      display: flex;
      align-items: center;

      &:first-child {
        margin-right: 18px;
      }

      @include _1150 {
        flex-direction: column;
        align-items: flex-start;

        &:first-child {
          margin-right: 80px;

          @include _480 {
            margin-right: 0;
            margin-bottom: 17px;
          }
        }
      }

      @include _675 {
        margin-right: 0;
      }
    }

    &__title {
      margin: 0;
      margin-right: 47px;
      font-weight: 400;
      font-size: 16px;
      color: var(--color-athens-gray);

      @include _1150 {
        font-size: 14px;
        margin-right: 0;
        margin-bottom: 7px;
      }
    }

    &__list {
      display: flex;
      align-items: center;
    }

    &__item {
      width: 70px;
      height: 48px;
      border-radius: 6px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      &:not(:last-child) {
        margin-right: 16px;
      }

      @include _1150 {
        width: 34px;
        height: 24px;
        border-radius: var(--border-radius);
      }

      &--apple {
        background-image: url("../img/apple.svg");
      }

      &--google {
        background-image: url("../img/google.svg");
      }

      &--master-card {
        background-image: url("../img/master-card.svg");
      }

      &--visa {
        background-image: url("../img/visa.svg");
      }
    }

    &__block--social {
      .footer-bottom__title {
        margin-right: 32px;
      }

      .footer-bottom__item {
        width: 48px;

        &:not(:last-child) {
          margin-right: 16px;

          @include _480 {
            margin-right: 24px;
          }
        }

        @include _1150 {
          width: 24px;
        }
      }
    }

    &__link {
      display: inline-block;
      width: 100%;
      height: 48px;
      filter: grayscale(35%);
      background-position: center;
      background-repeat: no-repeat;
      transition: filter .3s ease-in-out;
      background-size: cover;

      @include _1150 {
        width: 24px;
        height: 24px;
      }

      @include _480 {
        width: 32px;
        height: 32px;
      }

      &:hover {
        transition: filter .3s ease-in-out;
        filter: grayscale(0);
      }

      &--vk {
        background-image: url("../img/vk.svg");
      }

      &--fb {
        background-image: url("../img/fb.svg");
      }

      &--insta {
        background-image: url("../img/insta.svg");
      }

      &--you-tube {
        background-image: url("../img/you-tube.svg");
      }
    }

    &__copyright {
      margin: 0;
      padding-top: 24px;
      font-size: 12px;
      color: var(--color-gallery);

      @include _1150 {
        position: absolute;
        left: 0;
        padding-left: 16px;
        width: 100%;
        padding-top: 16px;

        &::before {
          @include ghost;
          height: 1px;
          left: 50%;
          transform: translateX(-50%);
          width: calc(100% - 32px);
          margin: 0 auto;
          background-color: rgba(205, 203, 203, 0.2);
        }
      }

      @include _675 {
        border-top: none;
      }
    }
  }
}
